body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: rgb(22, 22, 22);
  color: white;
}

body.light {
  background-color: lightblue;
  color: black;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.loading {
  font-size: 30px;
  text-align: center;
  margin: 30px 0px 30px 0px;
}

.header-container {
  display: flex;
  justify-content: space-between;
  background-color: rgb(1, 31, 68);
  color: whitesmoke;
  padding: 10px;
}

.search-container {
  display: flex;
  align-items: center;
  max-width: 50%;
}

.search-icon {
  margin-right: 5px;
}

.input {
  max-width: 80%;
}

.location {
  text-align: center;
  font-family: "Montserrat Alternates", sans-serif;
  font-weight: 600;
}

.days-container {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-evenly;
  align-items: center;
  font-family: "Montserrat Alternates", sans-serif;
  font-weight: 550;
  margin-bottom: 10px;
}

.day-margin {
  margin: 10px;
}

.vertical {
  border-left: 2px dashed white;
}

.vertical-light {
  border-left: 2px dashed black;
}

.container {
  display: flex;
  justify-content: flex-start;
  /* align-items: center; */
  overflow-x: auto;
  margin-bottom: 15px;
}

.center-content {
  text-align: center;
}

.font {
  font-family: "Montserrat Alternates", sans-serif;
  font-weight: 600;
}
